import React, { useState, useEffect, forwardRef } from "react";
import { withRouter, useHistory } from "react-router-dom";
import axios from "axios";
import { Form, Button, Row, Col } from "react-bootstrap";
import Snackbar from "@mui/material/Snackbar";
import MuiAlert from "@mui/material/Alert";
import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";
import MaterialTable, { MTablePagination } from "material-table";
import { TablePagination } from "@material-ui/core";
import BoQPopup from "./BoQPopup";
import { DataGrid, GridToolbar } from "@mui/x-data-grid";
import Dialog from "@mui/material/Dialog";
import Box from "@mui/material/Box";
import DialogContent from "@mui/material/DialogContent";
import CircularProgress from "@mui/material/CircularProgress";
import XLSX from "xlsx";
import { jsPDF } from "jspdf";
import autoTable from "jspdf-autotable";

import AddBox from "@mui/icons-material/AddBox";
import ArrowDownward from "@mui/icons-material/ArrowDownward";
import Check from "@mui/icons-material/Check";
import ChevronLeft from "@mui/icons-material/ChevronLeft";
import ChevronRight from "@mui/icons-material/ChevronRight";
import Clear from "@mui/icons-material/Clear";
import DeleteOutline from "@mui/icons-material/DeleteOutline";
import Edit from "@mui/icons-material/Edit";
import FilterList from "@mui/icons-material/FilterList";
import FirstPage from "@mui/icons-material/FirstPage";
import LastPage from "@mui/icons-material/LastPage";
import Remove from "@mui/icons-material/Remove";
import SaveAlt from "@mui/icons-material/SaveAlt";
import Search from "@mui/icons-material/Search";
import ViewColumn from "@mui/icons-material/ViewColumn";

const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

function ApprovedSpecificBoqBody(props) {
  let [users, setUsers] = useState([]);
  let [companies, setCompanies] = useState([]);
  const [openPopup, setOpenPopup] = useState(false);
  const [boqData, setBoqData] = useState([]);
  const [totalCost, setTotalCost] = useState();
  const [approvalHistory, setApproalHistory] = useState([]);
  let [severity, setSeverity] = useState("");
  let [alart, setAlart] = useState("");

  const axiosInstance = axios.create({
    baseURL: process.env.REACT_APP_BACKEND_URL,
  });

  let [form, setForm] = useState({});
  let [errors, setErrors] = useState({});
  let [open, setOpen] = useState(false);
  let [openDialog, setOpenDialog] = useState(false);

  let setField = (field, value) => {
    setForm({
      ...form,
      [field]: value,
    });
  };
  const [CapexPrAmount, setTotalCapexCost] = useState(0);
  const [OpexPrAmount, setTotalOpexCost] = useState(0);

  const tableIcons = {
    Add: forwardRef((props, ref) => <AddBox {...props} ref={ref} />),
    Check: forwardRef((props, ref) => <Check {...props} ref={ref} />),
    Clear: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
    Delete: forwardRef((props, ref) => <DeleteOutline {...props} ref={ref} />),
    DetailPanel: forwardRef((props, ref) => (
      <ChevronRight {...props} ref={ref} />
    )),
    Edit: forwardRef((props, ref) => <Edit {...props} ref={ref} />),
    Export: forwardRef((props, ref) => <SaveAlt {...props} ref={ref} />),
    Filter: forwardRef((props, ref) => <FilterList {...props} ref={ref} />),
    FirstPage: forwardRef((props, ref) => <FirstPage {...props} ref={ref} />),
    LastPage: forwardRef((props, ref) => <LastPage {...props} ref={ref} />),
    NextPage: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
    PreviousPage: forwardRef((props, ref) => (
      <ChevronLeft {...props} ref={ref} />
    )),
    ResetSearch: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
    Search: forwardRef((props, ref) => <Search {...props} ref={ref} />),
    SortArrow: forwardRef((props, ref) => (
      <ArrowDownward {...props} ref={ref} />
    )),
    ThirdStateCheck: forwardRef((props, ref) => (
      <Remove {...props} ref={ref} />
    )),
    ViewColumn: forwardRef((props, ref) => <ViewColumn {...props} ref={ref} />),
  };

  const columns = [
    {
      title: "ERP Code",
      field: "erpCode",
      filtering: false,
      editable: false,
      cellStyle: { width: 100, minWidth: 100, maxWidth: 100, height: 1 },
    },
    {
      title: "Rate Card Div",
      field: "rateCardDiv",
      editable: false,
      cellStyle: { width: 100, minWidth: 100, maxWidth: 100, height: 1 },
    },
    {
      title: "Item Discreption",
      field: "itemDiscreption",
      filtering: false,
      editable: false,
      cellStyle: { width: 300, minWidth: 300, maxWidth: 300, height: 1 },
    },
    {
      title: "UoM",
      field: "itemUom",
      editable: false,
      cellStyle: { width: 50, minWidth: 50, maxWidth: 50, height: 1 },
    },
    {
      title: "Unit Rate",
      field: "itemRate",
      editable: false,
      filtering: false,
      cellStyle: { width: 100, minWidth: 100, maxWidth: 100, height: 1 },
    },
    {
      title: "Quantity",
      field: "itemQty",
      editable: false,
      cellStyle: { width: 50, minWidth: 50, maxWidth: 50, height: 1 },
    },
    {
      title: "Item Total",
      field: "itemTotal",
      type: "currency",
      currencySetting: { currencyCode: "LKR" },
      editable: false,
      cellStyle: { width: 100, minWidth: 100, maxWidth: 100, height: 1 },
    },
    {
      title: "Capex/Opex",
      field: "capexopexLine",
      cellStyle: { width: 100, minWidth: 100, maxWidth: 100, height: 1 },
      editable: false,
    }, // Running Code
    {
      title: "Comment",
      field: "itemComment",
      editable: "onUpdate",
      cellStyle: { width: 100, minWidth: 100, maxWidth: 100, height: 1 },
    },
  ];

  //Approal History download table
  const columns_AH = [
    { field: "taskStatus", headerName: "Status", width: 250 },
    {
      field: "performedDate",
      headerName: "Performed Date",
      width: 200,
      valueGetter: (params) => params.row.performedDate.split("T")[0],
    },
    {
      field: "performedBy",
      headerName: "Performed by",
      width: 200,
      valueGetter: (params) => params.row.performedBy.name,
    },
    { field: "comment", headerName: "Comment", width: 500 },
  ];

  const columns_PDF_SITEDATA = [
    { dataKey: "siteID", header: "Site ID" },
    { dataKey: "siteName", header: "Site Name" },
    { dataKey: "assignedMobitelOfficer", header: "Assinged Mobitel officer" },
    { dataKey: "assignedSubcon", header: "Assigned Subcon" },
  ];

  const columns_PDF_APPROVAL = [
    { dataKey: "taskStatus", header: "Status" },
    { dataKey: "performedDate", header: "Performed Date" },
    { dataKey: "performedBy", header: "Performed by" },
    { dataKey: "comment", header: "Comment" },
  ];

  const columns_PDF_BOQ = [
    { header: "ERP Code", dataKey: "erpCode" },
    { header: "Rate Card Div", dataKey: "rateCardDiv" },
    { header: "Item Discreption", dataKey: "itemDiscreption" },
    { header: "UoM", dataKey: "itemUom" },
    { header: "Unit Rate", dataKey: "itemRate" },
    { header: "Quantity", dataKey: "itemQty" },
    { header: "Item Total", dataKey: "itemTotal" },
    { header: "Capex/Opex", dataKey: "capexopexLine" },
    { header: "Comment", dataKey: "itemComment" },
  ];

  const history = useHistory();
  useEffect(() => {
    // console.log(props)
    if (!props.location.data) {
      history.push("/completedTasks");
    } else {
      // console.log(props.location.data.row)
      setUsers([{ name: props.location.data.row.assignedMobitelOfficer.name }]);
      setCompanies([
        { companyName: props.location.data.row.assignedSubcon.companyName },
      ]);
      setForm({
        taskRef: props.location.data.row.taskRef,
        siteID: props.location.data.row.siteID,
        siteName: props.location.data.row.siteName,
        actualTaskStartDate: props.location.data.row.taskHistory
          .find((x) => x.taskStatus === "Task commenced")
          .performedDate.split("T")[0],
        actualTaskCompletedDate: props.location.data.row.taskHistory
          .find((x) => x.taskStatus === "Task completed")
          .performedDate.split("T")[0],
        taskAssignedDiv: props.location.data.row.taskAssignedDiv,
        assignedMobitelOfficer:
          props.location.data.row.assignedMobitelOfficer.name,
        workScope: props.location.data.row.workScope,
        assignedSubcon: props.location.data.row.assignedSubcon.companyName,
        verificationOneId: props.location.data.row.approvalPath[0].name,
        verificationTwoId: props.location.data.row.approvalPath[1].name,
        approvalId: props.location.data.row.approvalPath[2].name,
      });

      setBoqData(
        props.location.data.row.boqs[props.location.data.row.boqs.length - 1]
          .boqData
      );
      setTotalCost(
        props.location.data.row.boqs[props.location.data.row.boqs.length - 1]
          .totalCost
      );
      setApproalHistory(
        props.location.data.row.taskHistory.filter(
          (e) =>
            e.taskStatus === "BOQ Verified_0" ||
            e.taskStatus === "BOQ Verified_1" ||
            e.taskStatus === "BOQ Verified_2" ||
            e.taskStatus === "BOQ Approved" ||
            e.taskStatus === "BOQ submitted" ||
            e.taskStatus === "BOQ Rejected"
        )
      );

      // to set capex/opex amounts from backend
      setTotalCapexCost(
        props.location.data.row.boqs[props.location.data.row.boqs.length - 1]
          .CapexPrAmount
      );
      setTotalOpexCost(
        props.location.data.row.boqs[props.location.data.row.boqs.length - 1]
          .OpexPrAmount
      );

      // console.log(props.location.data.row.boqs[props.location.data.row.boqs.length - 1].totalCost)
    }
  }, []);

  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setOpen(false);
  };

  return (
    <div>
      <div className="content-wrapper">
        <div className="content-header">
          <section className="content-header">
            <div className="container-fluid">
              <div className="row mb-2">
                <div className="col-sm-6">
                  <h1>BOQ Task ID: {form.taskRef} </h1>
                </div>
              </div>
            </div>
          </section>
        </div>
        <section className="content">
          <div className="container-fluid">
            <Form>
              <Row>
                <Form.Group as={Col}>
                  <Form.Label>Site ID</Form.Label>
                  <Form.Control
                    disabled
                    value={form.siteID}
                    className="col-md-9"
                    type="text"
                    placeholder="Enter site ID"
                  />
                </Form.Group>
                <Form.Group as={Col}>
                  <Form.Label>Site Name</Form.Label>
                  <Form.Control
                    disabled
                    value={form.siteName}
                    type="text"
                    placeholder="Enter site Name"
                  />
                </Form.Group>
                <Form.Group as={Col} controlId=""></Form.Group>
              </Row>
              <Row>
                <Form.Group as={Col}>
                  <Form.Label>Task Assigned Division</Form.Label>
                  <Form.Control
                    disabled
                    value={form.taskAssignedDiv}
                    as="select"
                    defaultValue="Choose..."
                  >
                    <option>Choose...</option>
                    <option>Project_Radio</option>
                    <option>Project_Core</option>
                    <option>Project_TX</option>
                    <option>Project_IBS</option>
                    <option>Project_WiFi</option>
                  </Form.Control>
                </Form.Group>
                <Form.Group as={Col}>
                  <Form.Label>Assigned Mobitel Officer</Form.Label>
                  <Form.Control
                    disabled
                    as="select"
                    value={form.assignedMobitelOfficer}
                    defaultValue="Choose..."
                  >
                    <option>Choose...</option>
                    {users.map((opt) => (
                      <option value={opt._id}>{opt.name}</option>
                    ))}
                  </Form.Control>
                </Form.Group>
                <Form.Group as={Col}>
                  <Form.Label>Assigned subcontractor</Form.Label>
                  <Form.Control
                    disabled
                    value={form.assignedSubcon}
                    as="select"
                    defaultValue="Choose..."
                  >
                    <option>Choose...</option>
                    {companies.map((opt) => (
                      <option value={opt._id}>{opt.companyName}</option>
                    ))}
                  </Form.Control>
                </Form.Group>
              </Row>
            </Form>
            <MaterialTable
              title={"BOQ-" + form.siteID + "_" + form.taskRef}
              data={boqData}
              columns={columns}
              icons={tableIcons}
              editable={{
                onRowUpdate: (updatedRow, oldRow) =>
                  new Promise((resolve, reject) => {
                    const index = oldRow.tableData.id;
                    const updatedRows = [...boqData];
                    updatedRow.itemTotal =
                      updatedRow.itemRate * updatedRow.itemQty;
                    updatedRows[index] = updatedRow;
                    setTimeout(() => {
                      setBoqData(updatedRows);
                      resolve();
                    }, 2000);
                  }),
              }}
              options={{
                exportButton: true,
                pageSizeOptions: [boqData.length, 5, 10, 20],
                paginationType: "stepped",
                showTextRowsSelected: false,
                actionsColumnIndex: -1,
                filtering: false,
                tableLayout: "auto",
                search: false,
                rowStyle: {
                  backgroundColor: "#EEE",
                },
                headerStyle: {
                  backgroundColor: "#01579b",
                  color: "#FFF",
                },
                exportPdf: (columns, data) => {
                  const doc = new jsPDF({
                    orientation: "landscape",
                  });
                  autoTable(doc, {
                    head: [["Task ID : " + form.taskRef]],
                  });
                  autoTable(doc, {
                    theme: "grid",
                    head: [
                      [
                        "Site ID",
                        "Site Name",
                        "Assigned Mobitel Officer",
                        "Assigned Subcon",
                        "Capex Amount",
                        "Opex Amount",
                        "Total Value",
                      ],
                    ],
                    body: [
                      [
                        form.siteID,
                        form.siteName,
                        form.assignedMobitelOfficer,
                        form.assignedSubcon,
                        !CapexPrAmount || CapexPrAmount === ""
                          ? "Not Defined"
                          : CapexPrAmount.toLocaleString("en-US", {
                              style: "currency",
                              currency: "LKR",
                            }),
                        !OpexPrAmount || OpexPrAmount === ""
                          ? "Not Defined"
                          : OpexPrAmount.toLocaleString("en-US", {
                              style: "currency",
                              currency: "LKR",
                            }),
                        totalCost.toLocaleString("en-US", {
                          style: "currency",
                          currency: "LKR",
                        }),
                      ],
                    ],
                  });

                  autoTable(doc, {
                    head: [["BOQ"]],
                  });

                  autoTable(doc, {
                    theme: "grid",
                    columnStyles: {
                      itemRate: { halign: "right" },
                      itemTotal: { halign: "right" },
                    },
                    columns: columns_PDF_BOQ,
                    body: data,
                  });

                  autoTable(doc, {
                    head: [["Approval Hirarchy"]],
                  });
                  autoTable(doc, {
                    theme: "grid",
                    columns: columns_PDF_APPROVAL,
                    body: approvalHistory.map((obj) => ({
                      ...obj,
                      performedBy: obj.performedBy.name,
                    })),
                  });

                  const today = new Date();
                  const formattedDate = `${today.getFullYear()}${(
                    today.getMonth() + 1
                  )
                    .toString()
                    .padStart(2, "0")}${today
                    .getDate()
                    .toString()
                    .padStart(2, "0")}`;
                  doc.save(
                    `BOQ_${form.taskRef}_${`${new Date().getFullYear()}${(
                      new Date().getMonth() + 1
                    )
                      .toString()
                      .padStart(2, "0")}${new Date()
                      .getDate()
                      .toString()
                      .padStart(2, "0")}`}.pdf`
                  );
                  // console.log(`BOQ_${form.taskRef}_${`${new Date().getFullYear()}${(new Date().getMonth() + 1).toString().padStart(2, '0')}${new Date().getDate().toString().padStart(2, '0')}`}.pdf`);
                },
              }}
              components={{
                Pagination: (props) => (
                  <>
                    <Grid
                      container
                      style={{ pading: 300, background: "#f5f5f5" }}
                    >
                      <Grid sm={9} item>
                        <Typography align="right" variant="subtitle1">
                          Capex Amount
                        </Typography>
                        <Typography align="right" variant="subtitle1">
                          Opex Amount
                        </Typography>
                        <Typography
                          align="right"
                          fontWeight="bold"
                          variant="subtitle1"
                        >
                          Total Amount
                        </Typography>
                      </Grid>

                      <Grid sm={3} align="center">
                        <Typography align="right" variant="subtitle1">
                          {CapexPrAmount
                            ? "LKR " + parseFloat(CapexPrAmount)
                            : "LKR 0.00"}
                        </Typography>
                        <Typography align="right" variant="subtitle1">
                          {OpexPrAmount
                            ? "LKR " + parseFloat(OpexPrAmount)
                            : "LKR 0.00"}
                        </Typography>
                        <Typography
                          align="right"
                          fontWeight="bold"
                          variant="subtitle1"
                        >
                          {"LKR " + totalCost}
                        </Typography>
                      </Grid>
                    </Grid>
                    <TablePagination {...props} />
                  </>
                ),
              }}
            ></MaterialTable>
            <br />
            <Row>
              <Form.Group as={Col}>
                <Form.Label>Approval History</Form.Label>
                <div style={{ height: 250, width: "100%" }}>
                  <DataGrid
                    getRowId={(history) => history._id}
                    rows={approvalHistory}
                    columns={columns_AH}
                    pageSize={5}
                    rowsPerPageOptions={[5]}
                    rowHeight={30}
                    headerHeight={40}
                  />
                </div>
              </Form.Group>
            </Row>
          </div>
        </section>
      </div>

      <Snackbar
        open={open}
        autoHideDuration={6000}
        onClose={handleClose}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
      >
        <Alert onClose={handleClose} severity={severity} sx={{ width: "100%" }}>
          {alart}
        </Alert>
      </Snackbar>
      <Dialog
        open={openDialog}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogContent>
          <Box sx={{ display: "flex" }}>
            <CircularProgress />
          </Box>
        </DialogContent>
      </Dialog>
    </div>
  );
}

export default withRouter(ApprovedSpecificBoqBody);
